                p.mb-0 Esto implica que la diferenciación siempre está presente en el marco de la garantía de los derechos, incluyendo el reconocimiento de las particularidades de los grupos y las acciones necesarias para alcanzar la igualdad y la equidad. Estas medidas diferenciadas, conocidas como acciones afirmativas, están encuadradas dentro de las obligaciones de garantizar, proteger, promover y respetar los derechos humanos. En este contexto, los principios de universalidad, igualdad y no discriminación sirven como referentes para comprender y transformar la realidad de las personas de grupos especialmente protegidos, buscando alcanzar el nivel más alto de salud posible.
            .col-lg-5: img(src='@/assets/curso/temas/47.png', alt='')
