              p.mb-0 La atención en salud a la población de los sectores LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónicas debe basarse en el respeto, la igualdad y cero discriminación. Para que el personal de salud cumpla efectivamente con este derecho, se pueden considerar algunas recomendaciones específicas.
      p.mb-5(data-aos='fade-right') Dicho lo anterior, la atención en salud a la población de los sectores LGBTIQ+ y orientaciones sexuales e identidades de género no hegemónicas, debe estar basada en respeto, igualdad y cero discriminaciones. Para que el talento humano en salud dé cumplimiento a este derecho, se pueden tener en cuenta algunas recomendaciones como:
      .bg-4-2.p-5.mb-5
        SlyderE(indicadores="derecha").mt-3
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Atención inclusiva y valorativa
              p #[b Descripción:] Durante la atención, el talento humano en salud debe reconocer y valorar a las personas teniendo en cuenta sus características y diferencias, prestando una atención sin discriminación y con un ambiente agradable de continua interacción.  
              p #[b Ejemplo:] Al iniciar la consulta, preguntar por la forma en cómo se identifica respecto a la identidad de género (femenino, masculino, trans femenina, trans masculino, no binaria, queer), los pronombres que prefiere que se usen en la interacción (ella, él, elle). Finalmente, el sexo asignado al nacer: hombre, mujer o intersexual.
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/49.png', alt='')
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Empatía profesional
              p #[b Descripción:] El profesional de la salud debe tener en todo momento empatía, es decir, percibir lo que la persona siente y ponerse en su lugar. 
              
                |#[b Ejemplo_10:] Preguntar cómo quiere ser nombrada, no suponer. Dar la libertad que la persona exprese su nombre identitario, incluso si este difiere del nombre jurídico (el que aparece en su documento de identidad).
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/50.png', alt='')
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Ambiente amigable
              p #[b Descripción:] Propiciar en todo momento un ambiente de amabilidad, cortesía y sinceridad positiva.  
                | #[b Ejemplo_11:] Demostrar actitud de aceptación cuando la persona exprese su identidad de género independiente del aspecto físico y los datos jurídicos.
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/51.png', alt='')
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Garantía de servicio adecuado
              p #[b Descripción:] El personal de salud debe garantizar la prestación del servicio con tiempos requeridos y sin acortar el momento de consulta.  
                br
                | #[b Ejemplo_12:] La persona LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónicas debe recibir tiempo de consulta no menor al tiempo de consulta que se ofrece a cualquier persona usuaria del sistema de salud.
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/52.png', alt='')
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Tratamiento acorde a necesidades
              p #[b Descripción:] La persona debe recibir todas las opciones del plan de tratamiento acorde con sus necesidades, contar con apoyo y ayuda de manera prioritaria y recibir soluciones ante sus necesidades de atención en salud.  
                br
                |#[b Ejemplo 13 y 14: Ejemplo_13:] Si la persona LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónicas accede el servicio de salud bajo su condición como afiliado del sistema general de seguridad social en salud, se debe realizar el plan de tratamiento acorde con las tecnologías en salud contenidas en el plan de beneficios vigente. 
                br
                |#[b Ejemplo_14:] Si la persona LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónica ingresa en modalidad evento, se debe realizar la atención acorde con las tecnologías en salud pactadas en el acuerdo de voluntades con las entidades responsables de pago. En ningún caso se negará el servicio al que tenga derecho.
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/53.png', alt='')
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Autonomía del paciente
              p #[b Descripción:] La persona cuenta con toda la autonomía y las decisiones que tome se darán de manera libre sin que requiera que el personal de salud dé una ratificación o validación de éstas. En ningún momento se inducirá a la persona a cambiar de decisión o actuar de manera contraria a su propia voluntad.  
                br
                | #[b Ejemplo_15:] Si la persona LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónicas va a recibir un procedimiento quirúrgico, se deberá entregar y explicar el consentimiento informado. La persona debe leer y es su decisión si acepta de forma libre y consciente ser sometida a proceso terapéutico después de habérsele diagnosticado adecuadamente su situación de salud.
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/54.png', alt='')
          .row
            .col-lg-7.mb-4.mb-md-0
              h4(style="color: #0F5FFE ").mb-4 Información clara y accesible
              p #[b Descripción:] El personal de salud debe brindar información de una manera comprensible, completa, accesible, con veracidad y oportunidad en torno a los servicios prestados. Esta información debe contener la ruta de atención a seguir, duración del tratamiento (en caso de que aplique), los posibles resultados y todas las implicaciones del proceso de atención.
            .col-lg-5.col-7
              figure
                img(src='@/assets/curso/temas/55.png', alt='')
      .row.justify-content-center.mb-5
