      p.mb-5(data-aos='fade-right') Tener en cuenta las orientaciones generales para el manejo y abordaje de violencia física y sexual:
      .titulo-sexto.color-acento-contenido(data-aos='fade-right')
        h5 Figura 4. 
        span Orientación para el manejo y abordaje de violencia física y sexual:
      .mn.bg-4.mb-4
        .row.justify-content-center.mb-5
          .col-lg-8
            img(src='@/assets/curso/temas/63.svg', alt='')
            
      .row.justify-content-center.mb-4
        .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
          .tarjeta.p-5(style="background-color: #2c3e50 ").mb-4
            p.mb-0.text-white Durante la atención en salud, es crucial detectar todas las situaciones de violencia que puedan condicionar o perpetuar las afecciones por las cuales consultan las personas LGBTIQ+ y con orientaciones e identidades de género no hegemónicas. Es importante recordar que la violencia sexual también puede ocurrir en contextos de actividades sexuales remuneradas, lo cual debe ser atendido conforme a las rutas vigentes y con los enfoques pertinentes.
          p.mb-3(data-aos='fade-right') Como complemento a los anteriores, se cuenta con el #[b(style="color: #0F5FFE ") Enfoque de desarrollo humano], por medio del cual se garantiza la creación de los escenarios culturales, sociales y económicos para el respeto y la realización de los derechos de los sectores sociales LGBTIQ+ y con orientaciones sexuales e identidades de género      no hegemónicas., permitiendo así, la potencialización de las capacidades en torno a lo social, económico y cultural; anclado esto al cambio de imaginarios que hacen parte de las vivencias. #[b(style="color: #0F5FFE ") (Ministerio del Interior, 2018). ] 
          .row.justify-content-center.align-items-center.mb-3
            .col-lg-1.d-lg-block.d-none(data-aos="fade-right"): img(src='@/assets/curso/temas/dots.svg', alt='')
            .col-lg-11
              p.mb-0 Una de las formas en la que se puede contribuir al Enfoque de desarrollo humano es en torno al desarrollo social, y es en este desarrollo en donde se pueden mejorar las condiciones de los servicios sociales básicos, como la salud, nutrición, educación primaria y planificación familiar.

        .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/temas/64.png', alt='')

      p.mb-5(data-aos='fade-right') #[b(style="color: #0F5FFE ") Ejemplo] El personal de salud puede aportar al enfoque de desarrollo humano en la medida que genera recomendaciones en torno a los modos, condiciones y estilos de vida saludables. Puede basar las recomendaciones en su práctica y experiencia. Sin embargo, es importante tener en cuenta las recomendaciones dispuestas por el Ministerio de Salud y Protección Social respecto a:

      .row.justify-content-center.mb-5
        .col-lg-5.d-lg-block.d-none(data-aos="fade-right"): img(src='@/assets/curso/temas/65.png', alt='')
        .col-lg-7(data-aos="fade-left")
          .acordion-modificado 
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Actividad física")
                p.mb-0 Decálogo de la actividad física disponible en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/actividad-fisica.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/actividad-fisica.aspx] Sugerir alternativas de actividad física que impliquen interacciones no sexistas, proponer roles equitativos en las actividades con todas las personas, hablar de referentes deportivos no hegemónicos e incluso sugerir la implementación de ejercicios que no refuercen estereotipos de masculinidad y femineidad.
              div(titulo="Nutrición y alimentación saludable")
                p.mb-0 Información sobre alimentación y nutrición, lactancia materna, control de deficiencia de micronutrientes, alimentación saludable, inocuidad y calidad de alimentos y política de seguridad alimentaria y nutricional en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-nutricional.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-nutricional.aspx] 
                  br
                  |Fortalecer conceptos positivos sobre la imagen corporal para prevenir trastornos alimenticios. Evaluar calidad de dieta y riesgo de malnutrición.
              div(titulo="Valoración del estado nutricional y estrategias")
                p.mb-0 Detectar problemas y diagnósticos nutricionales integrales. Definir estrategias de intervención y seguimiento adecuado. Control por nutricionista acorde con necesidades puntuales identificadas.
              div(titulo="Indicadores nutricionales para personas trans")
                p.mb-0 La Academy of Nutrition and Dietetics recomienda el uso adecuado de indicadores nutricionales según la fase de transición. Los hombres Trans pueden tener demandas metabólicas aumentadas debido al uso de testosterona.
              div(titulo="Prevención del consumo de tabaco")
                p.mb-0 Prevención del consumo de tabaco y evaluación del uso de tabaco y otras sustancias en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/somos-mas-sin-tabaco.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/somos-mas-sin-tabaco.aspx]
                  br
                  |Evaluar uso en los últimos 6 meses, 12 meses y 5 años.
              div(titulo="Peso saludable")
                p.mb-0 Consejos para mantener un peso saludable en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/peso-saludable.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/peso-saludable.aspx]
                  br
                  |Evaluar déficit de vitaminas, calorías y micronutrientes en personas con dificultades en aumentar de peso o masa muscular, fatiga o ansiedad.
              div(titulo="Lavado de manos")
                p.mb-0 Importancia del lavado de manos detallada en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/lavado-de-manos.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/lavado-de-manos.aspx]        
              div(titulo="Salud bucal, visual y auditiva")
                p.mb-0 Información sobre salud bucal, visual y auditiva en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-bucal-visual-auditiva.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-bucal-visual-auditiva.aspx] 
                  br
                  |Hombres trans pueden tener sensibilidad, sangrado, inflamación y dolor en las encías por el uso de testosterona. Evaluar enfermedades en tejidos blandos y duros, hábitos relacionados con la salud bucal.
      p.mb-5(data-aos='fade-right') #[b Enfoque de Orientaciones Sexuales e Identidades de Género Diversas.] Este enfoque que busca abordar y atender las diversas orientaciones sexuales e identidades de género, reconociendo y enfrentando la discriminación, marginación, exclusión y otras formas de violencia que afectan a las personas de los sectores LGBTIQ+. Se menciona en el Decreto 762/2018 en el contexto de la política pública nacional LGBTI.
      .row.justify-content-center.mb-5
        .col-lg-4.mb-lg-0.mb-3
          .bg-white.tarjeta.overflow-hidden.sombra.h-100
            img(src='@/assets/curso/temas/66b.png', alt='')
            .p-4
              .text-center
                h5.mb-4 Política Pública Nacional LGBTI
                p.mb-0 Política que parte de reconocer la vulneración histórica de los derechos fundamentales de los sectores LGBTI, afirmando la igual dignidad y la condición inalienable de sus derechos. Promueve el restablecimiento de derechos y la consecución de la equidad bajo un enfoque diferencial.
        .col-lg-4.mb-lg-0.mb-3
          .bg-white.tarjeta.overflow-hidden.sombra.h-100
            img(src='@/assets/curso/temas/67b.png', alt='')
            .p-4
              .text-center
                h5.mb-4 Condiciones diferenciales 
                p.mb-0 Consideración explícita dentro del enfoque que aborda cómo las condiciones específicas de las personas intersexuales afectan el ejercicio efectivo de sus derechos, reconociendo sus luchas y demandas relacionadas con vulneraciones en los escenarios de atención de salud.
        .col-lg-4.mb-lg-0.mb-3
          .bg-white.tarjeta.overflow-hidden.sombra.h-100
            img(src='@/assets/curso/temas/68b.png', alt='')
            .p-4
              .text-center
                h5.mb-4 Objetivo de atención en salud
                p.mb-0 Relacionado con la promoción y garantía del ejercicio de los derechos económicos, sociales y culturales. Enfatiza en la igualdad de acceso y no discriminación en servicios como salud, educación, trabajo, vivienda, recreación, deporte y cultura para personas LGBTI, adoptando medidas técnicas y económicas necesarias.
      
      figure.mb-5
        .video
          iframe(width="560" height="315" src="https://www.youtube.com/embed/pVl9PNZsJo8?si=VpSc7syq81ZC0_V0" title="El Enfoque de Orientaciones Sexuales e Identidades de Género Diversas" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
          //img(src='@/assets/curso/videoyt.png' alt='')
